import findIndex from 'lodash/findIndex';
import remove from 'lodash/remove';

const initialState = {
    diaryList: []
};

export default function(state = initialState, action) {
    switch (action.type) {
        case 'GET_ALL_DATA': {
            const { diaryList } = action.payload;

            return {
                ...state,
                diaryList: diaryList
            };
        }
        case 'ADD_LOGIC_ERROR': {
            const { diaryList } = action.payload;

            return {
                ...state,
                diaryList: [diaryList, ...state.diaryList]
            };
        }
        case 'EDIT_LOGIC_ERROR': {
            const { diaryList } = action.payload;

            let index = findIndex(state.diaryList, {id: diaryList.id});
            let newArr = [...state.diaryList];

            newArr.splice(index, 1, diaryList);

            return {
                ...state,
                diaryList: newArr
            };
        }
        case 'DIARY_DELETE': {
            const { id } = action.payload;

            let newArr = [...state.diaryList];

            remove(newArr, (i) => (i.id === id));

            return {
                ...state,
                diaryList: newArr
            };
        }
        default:
            return state;
    }
}