import React, {Component} from 'react';

import Button from '@material-ui/core/Button';

export default class Home extends Component {
    render() {
        const { children } = this.props;

        return (
            <div className='button'>
                <Button
                    variant="outlined"
                    color="primary"
                    style={{width: '100%'}}
                    type='submit'
                    {...this.props}
                >
                    {children}
                </Button>
            </div>
        )
    }
}