import React, {Component} from 'react';
import { Link } from 'react-router-dom'
import './style.sass';

export default class ListItem extends Component {
    render() {
        const { location, children, link } = this.props;

        return (
            <Link to={`${location}${link}`} className='list-item'>
                {children}
            </Link>
        )
    }
}