import {addDiary, editDiary, deleteDiary} from '../../api'

export const diaryEdit = (type, content) => {

    editDiary(content);

    return {
        type: type,
        payload: {
            diaryList: content
        }
    }
};

export const diaryAdd = (type, content) => {

    addDiary(content);

    return {
        type: type,
        payload: {
            diaryList: content
        }
    }
};

export const diaryDelete = (type, id) => {
    deleteDiary(id);

    return {
        type: type,
        payload: {
            id: id
        }
    }
};