import React, {Component} from 'react';

import { connect } from "react-redux";

import Page from '../../../UI/Page';
import Header from '../../../UI/Header';
import Add from '../../../UI/Add';
import ListItem from '../../../UI/ListItem';

import {Link} from "react-router-dom";

import AddIcon from '@material-ui/icons/Add';

class JournalErrors extends Component {

    constructor(props) {
        super(props);

        this.date = 0;
    }

    componentWillReceiveProps(nextProps, nextContext) {
        if(nextProps.diaryList.length !== this.props.diaryList.length) {
            this.date = 0;
        }
    }

    render() {
        const { match, diaryList, location } = this.props;

        return (
            <div className='journal-errors'>
                <Header
                    h1='Дневник ошибок'
                    {...this.props}
                />
                <Page>
                    <h2>{match.params.name}</h2>
                    {diaryList.length !== 0 ? diaryList.map((item, id) => {

                        let date = item.dt;
                        let showDate = true;

                        if(this.date !== date) {
                            this.date = date;
                            showDate = true;
                        } else {
                            showDate = false;
                        }

                        return (
                        <div key={item.id}>
                           {showDate && <div style={{margin: '10px 0'}} className="date">{date}</div>}
                            <ListItem
                                location={location.pathname}
                                link={'/view:' + item.id}
                            >
                                <div>
                                    <span>{item.logicError}</span>
                                    <div className="hr" style={{margin: '5px 0',height: 1, background: 'grey'}} />
                                    <span>{item.solution}</span>
                                </div>
                            </ListItem>
                        </div>)
                    }) : <p>Тут пока нет ни одной записи</p>}
                </Page>
                <Link to={location.pathname + '/add'}>
                    <Add><AddIcon /></Add>
                </Link>
            </div>
        )
    }
}

const mapStateToProps = state => {
    const { DiaryReducer } = state;
    return DiaryReducer
};

export default connect(mapStateToProps)(JournalErrors);