const initialState = {
    diaryList: []
};

export default function(state = initialState, action) {
    switch (action.type) {
        case 'GET_ALL_DATA': {
            const { diaryList } = action.payload;

            return {
                ...state,
                diaryList: diaryList
            };
        }
        default:
            return state;
    }
}