import React, {Component} from 'react';
import './style.sass';

import { connect } from "react-redux";
import { Link } from 'react-router-dom';
import Button from '../../UI/Button';

import Page from '../../UI/Page';
import Header from '../../UI/Header';

import { doDatabaseStuff } from '../../api'
import {getAllData} from "../../redux/actions/getAllDataAction";

class Home extends Component {

    componentDidMount() {
        doDatabaseStuff().then(res => {
            this.props.getAllData('GET_ALL_DATA', res)
        })
    }

    render() {
        return (
            <div className='home'>
                <Header
                    h1='Главная'
                    {...this.props}
                />
                <Page>
                    <Link to="/diary-list"><Button>Дневник</Button></Link>
                </Page>
            </div>
        )
    }
}

const mapStateToProps = state => {
    const { DiaryReducer } = state;
    return DiaryReducer
};

export default connect(mapStateToProps, {getAllData})(Home);